import { repeat } from '../shared-utils/scheduler'
import { events, Q } from './context'
import { scene } from './renderer'

repeat((tpf) => {
	Q.get('device').tpf = tpf
	Q.emit(events.FRAME)
	Q.painter.compose(scene).display(scene)
}, 'loop')


if (module.hot) {module.hot.accept(function(err) {
if (err) {
console.error(err);
}
});
}
